<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm12 md8 lg8>
        <v-stepper v-model="e1" class="ma-6">
          <v-stepper-header class="elevation-0">
            <v-stepper-step color="success"
                            :complete="e1 > 1"
