            </v-stepper-step>
          </v-stepper-header>
          <v-stepper-items>
            <v-stepper-content step="1">
              <v-card class="mb-12 elevation-0" height="100px">
                <v-form  class="ma-6">
                  <v-text-field
                      append-icon="mdi-email-check-outline"
                      label="输入注册时邮箱"
                      type="text"
                      v-model="item.phoneNumber"></v-text-field>
                </v-form>
              </v-card>
              <v-btn color="primary" @click="e1 = 2">下一步</v-btn>
              <v-btn text :to="{path:'/login'}">取消</v-btn>
            </v-stepper-content>
            <v-stepper-content step="2">
              <v-card class="mb-12 elevation-0" height="100px">
