                  <v-text-field
                      append-icon="mdi-code-braces-box"
                      label="输入验证码"
                      type="text"
                      v-model="item.phoneNumber"
                  ></v-text-field>
                </v-form>
              </v-card>
              <v-btn color="primary" @click="e1 = 3">下一步</v-btn>
              <v-btn text :to="{path:'/login'}">取消</v-btn>
            </v-stepper-content>
            <v-stepper-content step="3">
              <v-card class="mb-12 elevation-0" height="100px">
                <v-form  class="ma-6">
                  <v-text-field
                      append-icon="mdi-lock"
                      label="输入新密码"
                      type="password"
                      v-model="item.phoneNumber"
                  ></v-text-field>
                  <v-text-field
                      append-icon="mdi-lock-check"
                      label="确认新密码"
                      type="password"
                      v-model="item.phoneNumber"
                  ></v-text-field>
                </v-form>
              </v-card>
              <v-btn color="primary" @click="e1 = 1">下一步</v-btn>
              <v-btn text :to="{path:'/login'}">取消</v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: "PasswordForget",
  data() {
    return {
      e1: 1,
      loading: false,
      item: {
        username: "",
        password: "",
        nickName: "",
        phoneNumber: "",
        email: '',
      }
    }
  },
}
</script>

